<template>
    <div :style="{fontSize:fSize+'px'}">
        <slot></slot>
    </div>
</template>

<script>


    let defaultWidth = 1600,
        maxWidth = 2060,
        defaultFont = 16,
        minWidth = 768,
        minHeight = 500,
        defaultHeight = 800;

    export default {
        name: 'Resize',
        mounted() {
            window.addEventListener('resize', this.onResize.bind(this));
            this.$root.$emit('onResize');
        },
        data() {
            return {
                vW: window.innerWidth,
                vH: window.innerHeight
            }
        },
        computed: {
            fSize() {
                let fSize = 16;
                if (!this.$root.isMobile) {
                    fSize = 16 * Math.min(Math.max(minWidth, this.vW) / defaultWidth, Math.max(minHeight, this.vH) / defaultHeight);
                }
                return fSize;
            }
        },
        methods: {
            onResize() {
                this.$root.$emit('onResize');
                this.vW = window.innerWidth;
                this.vH = window.innerHeight;
            }
        }

    }
</script>




